import React, { useState, useEffect } from 'react';

// Base de Datos de Cursos: Se incluye directamente aquí para evitar el error de importación.
const courses = [
    {
        id: 1,
        nameCourse: "Domina los Agentes de IA en 30 días",
        instructor: "Juanito el huerfanito",
        description: "Aprende a construir agentes de IA avanzados utilizando las últimas herramientas y SDKs del mercado. Este curso te guiará a través de 8 proyectos prácticos.",
        duration: "10 horas",
        price: "Gratis",
        image: "https://placehold.co/400x200/4F46E5/ffffff?text=Agentes+IA",
        technologies: ["IA", "OpenAI"],
        rating: 4.3,
        students: 1200,
        syllabus: [
            {
                id: 101, // ID de la sub-sección
                title: "Introducción a los agentes de IA",
                videoUrl: "https://www.youtube.com/watch?v=iDHzQO2r3xo"
            },
            {
                id: 102,
                title: "Configuración del entorno y primeros pasos",
                videoUrl: "https://www.youtube.com/watch?v=dQw4w9WgXcQ" 
            }
        ]
    },
    {
        id: 2,
        nameCourse: "Estructuras de Datos Avanzadas",
        instructor: "Prof. Algoritmo",
        description: "Un curso profundo sobre la implementación y complejidad de árboles, grafos y tablas hash.",
        duration: "15 horas",
        price: "$50",
        image: "https://placehold.co/400x200/EC4899/ffffff?text=Estructuras+Datos",
        technologies: ["Algoritmos", "C++"],
        rating: 4.8,
        students: 500,
        syllabus: [
            { id: 201, title: "Introducción a Árboles AVL", videoUrl: "https://www.youtube.com/watch?v=tree1" },
            { id: 202, title: "Recorridos en Grafos (DFS/BFS)", videoUrl: "https://www.youtube.com/watch?v=graph2" }
        ]
    }
];

// Usamos un ID de curso fijo por defecto (1) para la demostración, ya que 'next/router' no está disponible.
const MOCK_COURSE_ID = 1; 

const SeeCourse = () => {
    // Definimos el estado del ID del curso. Lo iniciamos con el ID fijo para la demo.
    const [courseId, setCourseId] = useState(MOCK_COURSE_ID.toString()); 
    const [course, setCourse] = useState(null);
    const [loading, setLoading] = useState(true);
    
    // Función de navegación simulada, ya que Next.js Link/Router no funciona aquí.
    const navigateToWatch = (cId, sId, title) => {
        // En un entorno real de Next.js, usarías router.push(`/watch/${cId}/${sId}`);
        // Aquí usamos un mensaje para demostrar que se capturan ambos IDs correctamente.
        window.alert(`Simulación de navegación a la lección:\nCurso ID: ${cId}\nSección ID: ${sId}\nTítulo: ${title}`);
    };

    useEffect(() => {
        if (courseId) {
            // Buscamos el curso por su ID (como string)
            const foundCourse = courses.find(c => c.id.toString() === courseId);
            setCourse(foundCourse);
            setLoading(false);
        }
    }, [courseId]);

    // Función para cambiar el curso en la demostración
    const handleCourseChange = (e) => {
        setLoading(true);
        setCourseId(e.target.value);
    };

    if (loading) {
        return (
            <div className="flex justify-center items-center h-screen bg-gray-50">
                <div className="animate-spin rounded-full h-12 w-12 border-4 border-indigo-500 border-t-transparent"></div>
                <p className="ml-4 text-gray-700">Cargando curso...</p>
            </div>
        );
    }

    if (!course) {
        return (
            <div className="flex flex-col items-center justify-center h-screen p-8 text-center bg-red-50">
                <h1 className="text-3xl font-bold text-red-700 mb-4">Curso no encontrado</h1>
                <p className="text-gray-600 mb-6">El curso que buscas no existe o ha sido removido.</p>
                <button 
                    className="px-6 py-2 bg-indigo-600 text-white rounded-lg shadow-md hover:bg-indigo-700 transition duration-150"
                    onClick={() => window.alert('Simulando navegación a la lista de cursos')}
                >
                    Volver a los cursos
                </button>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gray-50 p-8"> 
             {/* Selector de curso para demostración */}
            <div className="max-w-4xl mx-auto mb-6">
                 <label htmlFor="course-select" className="block text-sm font-medium text-gray-700 mb-2">
                    Seleccionar Curso (Demo ID):
                </label>
                <select 
                    id="course-select"
                    value={courseId}
                    onChange={handleCourseChange}
                    className="mt-1 block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm rounded-md shadow-sm"
                >
                    {courses.map(c => (
                        <option key={c.id} value={c.id}>{c.id}: {c.nameCourse}</option>
                    ))}
                </select>
            </div>


            <article className="max-w-4xl mx-auto bg-white p-8 rounded-xl shadow-2xl">
                <h1 className="text-4xl font-extrabold text-indigo-700 mb-2">{course.nameCourse}</h1>
                <p className="text-lg text-gray-600 mb-6">Instructor: <span className="font-semibold">{course.instructor}</span></p>
                
                <img 
                    src={course.image} 
                    alt={course.nameCourse} 
                    className="w-full h-48 object-cover rounded-lg mb-6 shadow-md"
                    onError={(e) => { e.target.onerror = null; e.target.src = "https://placehold.co/800x200/4F46E5/ffffff?text=Imagen+No+Disponible"; }}
                />
                
                <p className="text-gray-700 mb-4"><strong>Descripción:</strong> {course.description}</p>
                <div className="flex justify-between items-center text-gray-500 mb-8 p-3 border-y">
                    <p>Duración: <span className="font-medium text-gray-700">{course.duration}</span></p>
                    <p>Precio: <span className="font-medium text-green-600">{course.price}</span></p>
                </div>
                
                {/* SECCIÓN DEL TEMARIO CORREGIDA */}
                <div className="technologies-see-course">
                    <h2 className="text-2xl font-bold text-gray-800 mb-4 border-l-4 border-indigo-500 pl-3">Temario del curso:</h2>

                    <ul className="space-y-2">
                        {course.syllabus.map((item, index) => (
                            <li 
                                key={item.id || index} 
                                className="bg-indigo-50 hover:bg-indigo-100 p-3 rounded-lg transition duration-200"
                            >
                                {/* SIMULACIÓN DE LINK:
                                  Usamos un botón con onClick para simular la navegación, demostrando la captura de course.id y item.id.
                                */}
                                <button
                                    onClick={() => navigateToWatch(course.id, item.id, item.title)}
                                    className="block text-indigo-700 font-semibold hover:text-indigo-900 text-left w-full"
                                >
                                    {item.title} 
                                    <span className="text-xs text-gray-500 ml-3"> (Simular Lección)</span>
                                </button>
                            </li>
                        ))}
                    </ul>
                </div>

            </article>
        </div>
    );
};

export default SeeCourse;